export default "import EmblaCarousel from 'embla-carousel';\nimport { setupInfiniteScroll, mockApiCall, SLIDE_TEMPLATE, createHTMLFromString } from './EmblaCarouselInfiniteScroll';\nimport { addPrevNextButtonClickHandlers } from '../../EmblaCarouselArrowButtons';\nimport '../css/base.css';\nimport '../css/sandbox.css';\nimport '../css/embla.css';\n\nconst OPTIONS = {};\n\nconst emblaNode = document.querySelector('.embla');\nconst viewportNode = emblaNode.querySelector('.embla__viewport');\nconst prevBtn = emblaNode.querySelector('.embla__button--prev');\nconst nextBtn = emblaNode.querySelector('.embla__button--next');\n\nconst emblaApi = EmblaCarousel(viewportNode, OPTIONS);\n\nconst onResize = () => emblaApi.reInit();\nconst startInfiniteScroll = setupInfiniteScroll(emblaApi, (loadMore, endInfiniteScroll, slideCount) => {\n    mockApiCall(1000, 2000, () => {\n        if (slideCount === 20)\n            return endInfiniteScroll();\n        \n        const fiveNewSlideNodes = Array.from(Array(5).keys());\n        const slideNodesToAdd = fiveNewSlideNodes\n            .map(() => SLIDE_TEMPLATE)\n            .map((template, index) => {\n            const slideNumber = slideCount + index + 1;\n            return template.replace('__SLIDE_NUMBER__', slideNumber.toString());\n        })\n            .map(createHTMLFromString);\n        \n        loadMore(slideNodesToAdd);\n    });\n});\n\naddPrevNextButtonClickHandlers(emblaApi, prevBtn, nextBtn);\nstartInfiniteScroll();\nwindow.addEventListener('resize', onResize);\n";